import img from './../assets/avaImg/nina.jpg';
import img1 from './../assets/avaImg/rick.jpg';
import img2 from './../assets/avaImg/emma.jpg';
import img3 from './../assets/avaImg/movie.jpg';

const ADD_MESSAGE = 'ADD_MESSAGE';
const DELETE_MESSAGE = 'DELETE_MESSAGE'

let initial = {
  messagesList: [
      {
      id: '0', 
      dialogname: 
      'Нина', 
      img: img,
      dialog: [
        {id: '0', message: "Jeremy! Oh, no. No. No. Oh, god, no. Oh god... It's fine. It's fine. It's fine. You're gonna be okay. The ring will bring you back. It's okay. It's okay. You're gonna be okay. You're gonna be okay, Jeremy. You're gonna be fine.", author: 'Нина'},
        {id: '1', message: 'I dont speak english as well as you are thinking', author: 'me'},
        {id: '2', message: 'learn it!!!!', author: 'Нина'}
      ]
    },
      {
      id: '1', dialogname: 'Дана', img: img3,
      dialog: [
        {id: '0', message: 'как дела', author: 'Дана'},
        {id: '1', message: '?', author: 'Дана'},
  
      ]
    },
      {
      id: '2', dialogname: 'Абрахам', img: img1,
      dialog: [
        {id: '0', message: 'я программист', author: 'Абрахам'},
        {id: '1', message: 'а ты?', author: 'Абрахам'},
  
      ]
    },
      {
      id: '3', dialogname: 'Пиральта', img: img2,
      dialog: [
        {id: '0', message: 'я работаю в полиции', author: 'Пиральта'},
        {id: '1', message: 'ты арестован', author: 'Пиральта'},
  
      ]
    }
  ]
}

let messagesReducer = (state = initial, action) => {
    switch (action.type) {
        case ADD_MESSAGE:{
          const findedIndex = state.messagesList.findIndex(list => list.id === action.dialogId);
          const newList = state.messagesList.map(list => { return {...list}});
          newList[findedIndex].dialog.push(action.messageBody);
          return {
            ...state,
            messagesList: newList
          }
        }
        case DELETE_MESSAGE:{
          const newDialog = state.messagesList[action.elementIndex].dialog.filter(el => el.id !== action.id);
          const newList = state.messagesList.map(list => { return {...list}});
          newList[action.elementIndex].dialog = newDialog;
          return {
            ...state,
            messagesList: newList
          }
        }
        default:
            return state;
    }
};

export let addMessageAC = (messageBody, dialogId) => {
  return {type: ADD_MESSAGE, messageBody, dialogId}
}

export let deleteMessage = (elementIndex, id) => ({type: DELETE_MESSAGE, elementIndex, id})
 
export default messagesReducer;

