import React, { useEffect, useState } from 'react';
import classes from './paginator.module.css';




let Paginator = (props) => {

    let portionSize = 10;

    let pagesCount = props.totalCount / props.pageSise;

    let pages = [];

    for (let i = 1; i <= Math.ceil(pagesCount); i++) {
        pages.push(i);
    };

    let protionCount = Math.ceil(pagesCount / portionSize);
    let [portionNumber, setPortionNumber] = useState(1);
    let leftPortionPageNum = (portionNumber - 1) * portionSize + 1;
    let rightPortionPageNum = portionNumber * portionSize;

    useEffect(() => {
        props.setCurrentPage(leftPortionPageNum)
    }, [leftPortionPageNum])

    console.log(leftPortionPageNum)

    let plusPages = () => {
        setPortionNumber(portionNumber + 1);
    };

    let minusPages = () => {
        setPortionNumber(portionNumber - 1);
    }


    return (
        <div className={classes.pageNumber}>
            {portionNumber > 1 &&
                <button onClick={minusPages}>назад</button>}
            {
                pages
                    .filter(p => p >= leftPortionPageNum && p <= rightPortionPageNum)
                    .map((p, index) => {
                        return <span key={index} className={props.carrentPage === p && classes.pageNumberBold} onClick={() => { props.setCurrentPage(p) }}> {p} </span>
                    })
            }

            {protionCount > portionNumber &&
                <button onClick={plusPages}>вперед</button>}
        </div>
    )
};

export default Paginator;