import { stopSubmit } from "redux-form";
import { ProfileAPI } from "../api/api";

const ADD_POST = 'ADD_POST';
const SET_USER_PROFILE = 'SET_USER_PROFILE';
const SET_STATUS = 'SET_STATUS';
const DELETE_POST = "DELETE_POST";
const SET_PHOTO = "SET_PHOTO";


let initial = {
    posts: [
        {message: 'Пример поста', likes: 'Нравится: 100', id: '1'}
    ],
    profile: null,
    status: '',
}

let profileReducer = (state = initial, action) => {
    switch (action.type) {
        case ADD_POST:
            return {
                ...state,
                newPLetter: '',
                posts: [...state.posts, {message: action.postBody, likes: 'Нравится: 3'}]
            }
        case DELETE_POST:
            return {
                ...state,
                posts: state.posts.filter(p => p.id != action.postId)
            }
        case SET_USER_PROFILE:
            return {
                ...state,
                profile: action.profile
            }
        case SET_STATUS: 
            return {
                ...state,
                status: action.status
            }
        case SET_PHOTO: 
            return {
                ...state,
                profile: {...state.profile, photos: action.photo}
            }
        default:
            return state;
    }
};

export let setUserProfile  = (profile) => { return { type: SET_USER_PROFILE, profile } }
export let addPostAC       = (postBody) => { return { type: ADD_POST, postBody} }
export let deletePostAC    = (postId) => { return { type: DELETE_POST, postId} }
export let setStatus       = (status) => { return {type: SET_STATUS, status} }
export let setPhoto        = (photo) => { return { type: SET_PHOTO, photo}}

export let getProfile = (userId) => async (dispatch) => {
        let response = await ProfileAPI.getProfile(userId);
            dispatch(setUserProfile(response.data))
}

export let getUserStatus = (userId) => async (dispatch) => {
       let response = await ProfileAPI.getStatus(userId);
            dispatch(setStatus(response.data))
}

export let upDataStatus = (status) => async (dispatch) => {
        try{
            let response = await ProfileAPI.updateStatus(status);
            if(response.data.resultCode === 0) {
                dispatch(setStatus(status))
            }
        }catch(error){
            alert(error)
        }
};

export let saveFile = (photo) => async (dispatch) => {
    let response = await ProfileAPI.savePhoto(photo);
        if(response.data.resultCode === 0) {
            dispatch(setPhoto(response.data.data.photos))
        }
}

export let saveInfo = (profile) => async (dispatch, getState) => {
    const userId = getState().Auth.id
    let response = await ProfileAPI.saveProfile(profile);
    debugger;
        if(response.data.resultCode === 0) {
            dispatch(getProfile(userId))
        } else {
            let message = response.data.messages.length > 0 ? response.data.messages[0]: 'Some error';
            dispatch(stopSubmit('edit-profile', {_error: message}))
        }
}




export default profileReducer;