import React, { createRef } from 'react';
import { Field, reduxForm } from 'redux-form';
import { maxLengthCreator, required } from '../../Utils/validator/validator';
import { Textarea } from '../coummon/Prilouder/formControls/formControls';
import classes from './MyPosts.module.css';
import Posts from './Posts';
import ava from '../../assets/avaImg/mal2.jpg';



const MyPosts = React.memo((props) => {

    let posts = props.posts;
    let addPostC = props.addPostC;

    let addPostBody = (value) => {
        addPostC(value.postBody)
    }


    let postsData = [...posts].reverse().map((p, index) => <Posts key={index} message={p.message} likes={p.likes} id={p.id} deletePost={props.deletePost} />)

    return (
        <div>
            <div className={classes.MyPostsForm}>
                <AddPostReduxForm onSubmit={addPostBody} />
            </div>
            <div className={classes.MyPosts}>
                <div>
                    <h2>
                        мои посты
                    </h2>
                </div>
                <div className={classes.Post}>
                    {postsData}
                </div>
            </div>
        </div>
    )
});

const maxLength200 = maxLengthCreator(200);

const addPostForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            <div className={classes.textareaButtonBlock}>
                <img src={ava} alt="user" />
                <div className={classes.textareaButton}>
                    <Field component={Textarea} cols="40" placeholder="Напишите свой пост..." name="postBody" validate={[maxLength200]} />
                    <button className={classes.buttonForm}>Опубликовать</button>
                </div>
            </div>
        </form>

    )
};

const AddPostReduxForm = reduxForm({ form: "messenger" })(addPostForm);



export default MyPosts;