import React from 'react';
import { reduxForm } from 'redux-form';
import { maxLengthCreator, required } from '../../../Utils/validator/validator';
import { createField, Input, Textarea } from '../../coummon/Prilouder/formControls/formControls';
import classes from './ProfileInfo.module.css'

const ProfileDataForm = (props) => {

    const profile = props.profile

    return (
        <form onSubmit={props.handleSubmit}>
            <div className={classes.profileForm}>
                <div>
                    <span className={classes.item}>Полное имя:</span>{createField("Полное имя", "fullName", Input, [])}
                </div>
                <div>
                    <span className={classes.item}>Обо мне:</span>{createField("о себе", "aboutMe", Textarea, [], null, null, 70, 5)}
                </div>
                <div>
                    <span className={classes.item}>Ищю работу:</span>{createField("", "lookingForAJob", Input, [], { type: 'checkbox' })}
                </div>
                <div>
                    <span className={classes.item}>Что умеете:</span>{createField("профессиянальные навыки", "lookingForAJobDescription", Textarea, [], null, null, 70, 5)}
                </div>
                <span className={classes.item}>Контакты:</span>
                {Object.keys(profile.contacts).map((contact, index) => <div key={index} className={classes.formInputs}>{createField(contact, "contacts." + contact, Input, [])}</div>)}
                <div className="error">
                    {props.error && props.error}
                </div>
                <button>Сохранить</button><label onClick={props.editFormInfo}>вернуться</label>
            </div>
        </form>
    )
};

const WithReduxForm = reduxForm({ form: "edit-profile" })(ProfileDataForm);


export default WithReduxForm;
