import React, { useState } from 'react';
import Prilouder from '../../coummon/Prilouder/Prilouder';
import classes from './ProfileInfo.module.css'
import ProfileData from './profileData';
import WithReduxForm from './profileDataForm';
import { NavLink } from 'react-router-dom';

import ava from '../../../assets/avaImg/mal2.jpg';
import user from '../../../assets/avaImg/user.png';
import messenger from '../../../assets/avaImg/messenger.png';
import people from '../../../assets/avaImg/people.png'


const ProfileInfo = (props) => {

    let [editForm, SetEditForm] = useState(false);

    const onSubmit = (formData) => {
        props.saveInfo(formData)
    }

    const editFormInfo = () => {
        SetEditForm(!editForm)
    }

    let profile = props.profile;

    if (!props.profile) {
        return <Prilouder />
    }

    const mainPhoto = (e) => {
        if (e.target.files.length) {
            props.saveFile(e.target.files[0]);
        }
    }

    return (
        <div className={classes.profileInfo}>
            <div className={props.isOuner ? classes.AvaImg : classes.AvaImgGuest}>
                <img src={profile.photos.large ? profile.photos.large : ava} alt="ava" />
                <div>
                    {props.isOuner && <div className={classes.addPhoto}>
                        <input name="file" type="file" id="file" onChange={(e) => mainPhoto(e)} />
                        <label for='file'>Добавить фото</label>
                    </div>}
                </div>
                <div className={classes.menu}>
                    <div className={classes.items}><NavLink activeClassName={classes.activeLink} to="/Profile"><img src={user} alt="user" /></NavLink></div>
                    <div className={classes.items}><NavLink activeClassName={classes.activeLink} to="/People"><img src={people} alt="people" /></NavLink></div>
                    <div className={classes.items}><NavLink activeClassName={classes.activeLink} to="/Messenger"><img src={messenger} alt="messages" /></NavLink></div>
                </div>
            </div>

            {editForm ? <WithReduxForm editFormInfo={editFormInfo} {...props} initialValues={profile} onSubmit={onSubmit} /> : <ProfileData {...props} editFormInfo={editFormInfo} isOuner={props.isOuner} />}

        </div>
    )
};

export default ProfileInfo;

