import { Redirect } from "react-router";
import { stopSubmit } from "redux-form";
import { AuthAPI, SecurityApi } from "../api/api";

const SET_USER_DATA = 'SET_USER_DATA';
const SET_CAPTCHA_URL = 'SET_CAPTCHA_URL';

let initial = {
    id: null, 
    email: null,
    login: null,
    isAuth: false,
    captcha: null
}

let authReducer = (state = initial, action) => {
    switch (action.type) {
        case SET_USER_DATA:
            return {
                ...state,
                ...action.data
            }
        case SET_CAPTCHA_URL:
            return {
                ...state,
                captcha: action.captchaUrl
            }
        default:
            return state;
    }
};

export let setUserData = (id, email, login, isAuth) => { return { type: SET_USER_DATA, data: {id, email, login, isAuth } }};
export let setCaptcha = (captchaUrl) => { return {type: SET_CAPTCHA_URL, captchaUrl}}

export let Auth = () => async (dispatch) => {
       let response = await AuthAPI.Auth();
            if (response.data.resultCode === 0) {
                let {id, email, login} = response.data.data;
                dispatch(setUserData(id, email, login, true))
            }
};

export let LogIn = (email, password, rememberMe, captcha) => async (dispatch) => {
        let response = await AuthAPI.Login(email, password, rememberMe = false, captcha = null);
            if (response.data.resultCode === 0) {
                dispatch(Auth())
            } else {
                if(response.data.resultCode === 10) {
                    dispatch(getCaptche())
                }
                let message = response.data.messages.length > 0 ? response.data.messages[0]: 'Some error';
                dispatch(stopSubmit('login', {_error: message}))
            }
};

export let LogOut = () => async (dispatch) => {
        let response = await AuthAPI.Logout();
        debugger;
            if (response.data.resultCode === 0) {
                dispatch(setUserData(null, null, null, false))
            }
}

export let getCaptche = () => async (dispatch) => {
    let response = await SecurityApi.getCatpchaUrl();
    const captcha = response.data.url;
    dispatch(setCaptcha(captcha))
}

export default authReducer;