import React from 'react';
import { NavLink } from 'react-router-dom';
import classes from './Header.module.css';
import logitype from '../../assets/avaImg/suanchik.png';
import out from '../../assets/avaImg/log-out.png'


const Header = (props) => {
    return (
        <header className={classes.header}>
            <img src={logitype} alt="" /><h1><span className={classes.suan}>suan</span><span className={classes.chik}>chik</span></h1>
            {props.isAuth ? <div className={classes.logBlock}><span className={`${classes.out} ${classes.span}`} onClick={props.LogOut}>Выйти</span></div> : <NavLink to={`/Login`}><div className={classes.logBlock}>Войти</div></NavLink>}
        </header>
    )
};


export default Header;