import React from 'react';
import { NavLink } from 'react-router-dom';
import classes from './NavBar.module.css';
import user from '../../assets/avaImg/user.png';
import messenger from '../../assets/avaImg/messenger.png';
import people from '../../assets/avaImg/people.png'

const Nav = () => {
    return (
        <nav className={classes.Nav}>
            <div className={classes.items}><NavLink activeClassName={classes.activeLink} to="/Profile"><img src={user} alt="user" /><div className={classes.item}>мой профиль</div></NavLink></div>
            <div className={classes.items}><NavLink activeClassName={classes.activeLink} to="/People"><img src={people} alt="" /><div className={classes.item}>людu</div></NavLink></div>
            <div className={classes.items}><NavLink activeClassName={classes.activeLink} to="/Messenger"><img src={messenger} alt="" /><div className={classes.item}>cообщения</div></NavLink></div>
            {/* <li><NavLink to="/News"><div className={classes.item}>Новости</div></NavLink>
                <li><NavLink to="/Music"><div className={classes.item}>Музыка</div></NavLink>
                <li><NavLink to="/Setting"><div className={classes.item}>Настройки</div></NavLink> */}
        </nav>
    )
};


export default Nav;