import React, { useState } from 'react'; import classes from './ProfileInfo.module.css';
import ProfileStatusWithHooks from './ProfileStatusWithHook';
import up from '../../../assets/avaImg/arrow-up-icon.png';
import down from '../../../assets/avaImg/arrow-up-icon-2.png'

const ProfileData = (props) => {

    let [editMode, SetEditMode] = useState(false);

    let profile = props.profile;

    const showContacts = () => {
        SetEditMode(!editMode)
    }

    let socialContacts = [];

    for (let contact in profile.contacts) {
        socialContacts.push(<div key={contact[socialContacts.length]}><span className={classes.item}>{contact}: </span>{profile.contacts[contact]}</div>)
    };

    return (
        <div className={classes.userInfo}>
            <div>
                <span className={classes.userName}>{profile.fullName}</span>
                {props.isOuner && <span onClick={props.editFormInfo} className={classes.editButton}>Редактировать</span>}
                <ProfileStatusWithHooks status={props.status} upDataStatus={props.upDataStatus} /><hr />
            </div>
            <div>
                <div><span className={classes.item}>Обо мне: </span>{profile.aboutMe}</div>
            </div>
            <div>
                <div><span className={classes.item}>ищу работу: </span>{profile.lookingForAJob ? "да" : "нет"}</div>
                <div><span className={classes.item}>проф. навыки: </span>{profile.lookingForAJobDescription}</div>
            </div>
            <div>
                <div onClick={showContacts} className={classes.contactsHead}><span>контакты:</span><span>{!editMode ? <img src={down} alt="" /> : <img src={up} alt="" />}</span></div>
                {
                    editMode ? socialContacts : ''
                }
            </div>
        </div>
    )
};


export default ProfileData

